	import myPicker from "@/components/myPicker.vue";
	import rangeButton from "@/components/rangeButton.vue";
	import swithButton from "@/components/switchButton.vue";
	// import myCheckboxGroup from "@/components/myCheckboxGroup.vue";
	const JsyServer = require("services/jsy-server.js");
	let _this,_customerId;
	export default {
		components:{
			myPicker,
			rangeButton,
			swithButton
		},
		data() {
			return {
				postData:{
					companyName:'', //  客户名
					companyAddress:'', //  地址
					companyLongitude:'', //  经度
					companyLatitude:'', //  纬度
					regionCode: -1, //  区域编码
					companyTypeCode: -1, //  客户类型编码
					companyScaleCod: -1, //  客户规模编码
					companySourceCode: -1, //  客户来源编码
					cooperationIntentionCode: -1, // 合作意向
					coordinateCode: -1, //  配合度
					managementPositionCode: -1, // 经营定位
					realName:'', //  姓名
					phone:'', // 电话
				},
				region:[],
				companyType:[],
				companyScale:[],
				companySource:[],
				cooperationIntention:[],
				coordinate:[],
				managementPosition:[],

				
			};
		},
		onLoad:function(){
			_this = this
			this.getRegion()
			this.getSource()
			this.getType()
			this.getCooperationIntention()
			this.getManagementPosition()
			this.getCoordinate()
			this.getScale()
			
		},
		methods:{
			getRegion:function(){
				JsyServer.getRegion().then(res => {
				  console.log(res);
				  _this.region = res.data.data.list
				}).catch(err => {
				  wx.showToast({
				    title: err.data.description,
				    icon: 'none'
				  });
				});
			},
			getCoordinate:function(){
				JsyServer.getCoordinate().then(res => {
				  console.log(res);
				  _this.coordinate = res.data.data.list
				}).catch(err => {
				  wx.showToast({
				    title: err.data.description,
				    icon: 'none'
				  });
				});
			},
			getType:function(){
				JsyServer.getType().then(res => {
				  console.log(res);
				  _this.companyType = res.data.data.list
				}).catch(err => {
				  wx.showToast({
				    title: err.data.description,
				    icon: 'none'
				  });
				});
			},
			getSource:function(){
				JsyServer.getSource().then(res => {
				  console.log(res);
				  _this.companySource = res.data.data.list
				}).catch(err => {
				  wx.showToast({
				    title: err.data.description,
				    icon: 'none'
				  });
				});
			},
			getCooperationIntention:function(){
				JsyServer.getCooperationIntention().then(res => {
				  console.log(res);
				  _this.cooperationIntention = res.data.data.list
				}).catch(err => {
				  wx.showToast({
				    title: err.data.description,
				    icon: 'none'
				  });
				});
			},
			getScale:function(){
				JsyServer.getScale().then(res => {
				  console.log(res);
				  _this.companyScale = res.data.data.list
				}).catch(err => {
				  wx.showToast({
				    title: err.data.description,
				    icon: 'none'
				  });
				})
			},
			getManagementPosition:function(){
				JsyServer.getManagementPosition().then(res => {
				  console.log(res);
				  _this.managementPosition = res.data.data.list
				}).catch(err => {
				  wx.showToast({
				    title: err.data.description,
				    icon: 'none'
				  });
				});
			},
		
			
            tapPickerEvent:function(arrName,id){
            	switch (arrName){
					case "region":
					    console.log(id)
					    this.postData.regionCode = id
					    break;
            		case "companyType":
            		    console.log(id)
            		    this.postData.companyTypeCode = id
            		    break;
				    case "companyScale":
				        console.log(id)
				        this.postData.companyScaleCode = id
				        break;
					case "companySource":
					    console.log(id)
					    this.postData.companySourceCode = id
					    break;
					case "cooperationIntention":
					    console.log(id)
					    this.postData.cooperationIntentionCode = id
					    break;
					case "coordinate":
					    console.log(id)
					    this.postData.coordinateCode =id
						break;
					case "managementPosition":
					    console.log(id)
					    this.postData.managementPositionCode =id
						break;
            	}
            },
			
			
			addrPosition:function(index){
				console.log("here")
				if (index ==1){
					uni.chooseLocation({
					    success: function (res) {
					       
							_this.postData.companyAddress = res.address
					        console.log('详细地址：' + res.address);
							_this.postData.companyLatitude = res.latitude
					        console.log('纬度：' + res.latitude);
							_this.postData.companyLongitude = res.longitude
					        console.log('经度：' + res.longitude);
					    }
					});
				}
				
				
			},
			
			bindCancel:function(){
				uni.navigateBack({
					delta: 1
				});
			},
			formSubmit:function(e){
				let data =this.postData
				console.log(this.postData)
				console.log(uni.getStorageSync('token'))
                JsyServer.sellCusmterCreated(data).then(res => {
                  console.log(res);
                }).catch(err => {
                  wx.showToast({
                    title: err.data.description,
                    icon: 'none'
                  });
                });
			}
			
		}
	}
